<script>
import { select } from 'd3'
import _ from 'lodash'

export default {
  name: 'JetportSection',
  props: ['xScale', 'keyDates', 'addScrollTarget'],
  mounted() {
    this.buildContent()
    //this.addScrollTarget('#jetport #airplanes')
  },
  computed: {
    path: () => `${window.location.pathname}`,
  },
  methods: {
    buildContent() {
      select('#jetport')
        .selectAll('.key-date')
        .data(this.keyDates)
        .enter()
        .append('div')
        .attr('class', 'key-date')
        .html(
          d => `<h5 style="color:${_.get(
            d.style,
            'color',
            'var(--key-date-pink)'
          )};">${d.keyDate.title}</h5>
      <p>${d.keyDate.text}</p>`
        )
        .style(
          'left',
          d =>
            `${_.get(d.style, 'left') +
              _.get(d.style, 'adjustHorizontal', 0)}px`
        )
        .style('top', d => `${_.get(d.style, 'top', 100)}px`)
        .style('bottom', d => `${_.get(d.style, 'bottom', 100)}`)
        .style('width', d => `${_.get(d.style, 'width', 400)}px`)
    },
  },
}
</script>
